import React, { Component } from 'react';
import { authAPI } from '../../utils/api';

class Login extends Component {
    constructor(props) {
        super(props);
        this.state = {
            email: '',
            password: '',
            error: '',
            loading: false
        };
    }

    handleSubmit = async (e) => {
        e.preventDefault();
        this.setState({ error: '', loading: true });

        try {
            const response = await authAPI.login(this.state.email, this.state.password);
            // í† í°ê³¼ ì‚¬ìš©ì ì •ë³´ ì €ì¥
            authAPI.saveToken(response.token, response.user);

            if (this.props.onLoginSuccess) {
                this.props.onLoginSuccess();
            }
        } catch (err) {
            this.setState({ error: err.message || 'ë¡œê·¸ì¸ ì‹¤íŒ¨' });
        } finally {
            this.setState({ loading: false });
        }
    };

    render() {
        const { email, password, error, loading } = this.state;
        const { onSwitchToRegister } = this.props;

        return (
            <div style={{ maxWidth: '400px', margin: '50px auto', padding: '20px', border: '1px solid #ddd', borderRadius: '8px', backgroundColor: '#fff' }}>
                <h2 style={{ textAlign: 'center', marginBottom: '20px' }}>ë¡œê·¸ì¸</h2>

                {error && (
                    <div style={{ padding: '10px', marginBottom: '15px', backgroundColor: '#f8d7da', color: '#721c24', border: '1px solid #f5c6cb', borderRadius: '4px' }}>
                        {error}
                    </div>
                )}

                <form onSubmit={this.handleSubmit}>
                    <div style={{ marginBottom: '15px' }}>
                        <label style={{ display: 'block', marginBottom: '5px', fontWeight: 'bold' }}>ì´ë©”ì¼</label>
                        <input
                            type="email"
                            value={email}
                            onChange={(e) => this.setState({ email: e.target.value })}
                            required
                            style={{ width: '100%', padding: '8px', border: '1px solid #ddd', borderRadius: '4px', fontSize: '14px' }}
                        />
                    </div>

                    <div style={{ marginBottom: '15px' }}>
                        <label style={{ display: 'block', marginBottom: '5px', fontWeight: 'bold' }}>ë¹„ë°€ë²ˆí˜¸</label>
                        <input
                            type="password"
                            value={password}
                            onChange={(e) => this.setState({ password: e.target.value })}
                            required
                            style={{ width: '100%', padding: '8px', border: '1px solid #ddd', borderRadius: '4px', fontSize: '14px' }}
                        />
                    </div>

                    <button
                        type="submit"
                        disabled={loading}
                        style={{
                            width: '100%',
                            padding: '10px',
                            backgroundColor: loading ? '#ccc' : '#007bff',
                            color: '#fff',
                            border: 'none',
                            borderRadius: '4px',
                            fontSize: '16px',
                            cursor: loading ? 'not-allowed' : 'pointer'
                        }}
                    >
                        {loading ? 'ë¡œë”© ì¤‘...' : 'ë¡œê·¸ì¸í•˜ê¸°'}
                    </button>
                </form>

                <div style={{ textAlign: 'center', marginTop: '15px' }}>
                    <small>
                        ê³„ì •ì´ ì—†ìœ¼ì‹ ê°€ìš”?{' '}
                        <button
                            onClick={onSwitchToRegister}
                            style={{
                                background: 'none',
                                border: 'none',
                                color: '#007bff',
                                textDecoration: 'underline',
                                cursor: 'pointer',
                                padding: 0,
                                fontSize: 'inherit'
                            }}
                        >
                            íšŒì›ê°€ì…
                        </button>
                    </small>
                </div>
            </div>
        );
    }
}

export { Login };
